/* CSS Variables and Modern Design System */
:root {
  --accent: #3b82f6;
  --text-primary: #1f2937;
  --text-secondary: #6b7280;
  --text-muted: #9ca3af;
  --border-light: #e5e7eb;
  --border-focus: var(--accent);
  --bg-white: #ffffff;
  --bg-gray-50: #f9fafb;
  --bg-gray-100: #f3f4f6;
  --shadow-md: 0 4px 6px rgba(0,0,0,0.1);
  --radius-md: 6px;
  --radius-lg: 8px;
  --radius-xl: 12px;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Arial, sans-serif;
  background: var(--bg-gray-50);
  color: var(--text-primary);
  margin: 0;
  min-height: 100vh;
}
.main-wrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  padding-bottom: 5vh;
}
.dashboard-container {
  max-width: 900px;
  margin: 2rem auto;
  padding: 2rem;
  background: var(--bg-white);
  border-radius: var(--radius-xl);
  box-shadow: var(--shadow-md);
}
.dashboard-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}
.accounts-list {
  margin-top: 2rem;
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
}
.account-card {
  background: var(--bg-gray-100);
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-md);
  padding: 1.3rem 2rem 1.3rem 1.3rem;
  min-width: 265px;
  margin-bottom: 1rem;
  display: flex;
  flex-direction: column;
  gap:0.5rem;
}
.form-container {
  max-width: 420px;
  margin: 3rem auto;
  padding: 2rem 2rem 2.4rem 2rem;
  background: var(--bg-white);
  border-radius: var(--radius-xl);
  box-shadow: var(--shadow-md);
  display: flex;
  flex-direction: column;
  gap: 1.3rem;
}
label {
  display: block;
  color: var(--text-secondary);
  font-size: 0.98rem;
  font-weight: 500;
  margin-bottom: 0.19rem;
}
input, select {
  width: 100%;
  height: 2.35rem;
  font-size: 1.05rem;
  border: 1px solid var(--border-light);
  border-radius: var(--radius-md);
  padding: 0.5rem;
  margin-bottom: 0.2rem;
  background: var(--bg-gray-50);
  transition: all 0.18s ease;
}
input:focus, select:focus {
  border-color: var(--border-focus);
  outline: none;
  background: white;
  box-shadow: 0 0 0 2px rgba(59, 130, 246, 0.12);
}
.btn-primary {
  background: var(--accent);
  color: white;
  border: 1px solid var(--accent);
  padding: 0.5rem 1.2rem;
  border-radius: var(--radius-md);
  font-weight: 500;
  margin-top: 0.6rem;
  cursor: pointer;
  font-size: 1rem;
  transition: background 0.15s, transform 0.15s;
}
.btn-primary:hover {
  background: #2461b9;
  transform: translateY(-2px);
}
.btn-secondary {
  background: transparent;
  color: var(--accent);
  border: 1px solid var(--accent);
  border-radius: var(--radius-md);
  padding: 0.5rem 1.1rem;
  font-weight: 480;
  transition: all 0.17s;
  cursor: pointer;
}
.btn-secondary:hover {
  background: var(--accent);
  color: #fff;
}
.error {
  color: #dc2626;
  background: #fbeaea;
  border-left: 4px solid #dc2626;
  margin-top: 6px;
  padding: 0.4rem 0.7rem;
  border-radius: var(--radius-md);
  font-size: 0.98rem;
}
.empty-state {
  color: var(--text-muted);
  text-align: center;
  font-style: italic;
  padding: 1.7rem;
  font-size: 1.08rem;
}
.label {
  font-weight: 500;
  color: var(--text-secondary);
  font-size: 0.98rem;
}
.action-buttons > button {
  flex: 1;
  min-width: 102px;
}
.tx-table {
  background: #fff;
  box-shadow: var(--shadow-md);
  border-radius: var(--radius-lg);
  width: 100%;
  border-spacing: 0;
  font-size: 1rem;
}
.tx-table th {
  color: var(--accent);
  padding: 0.52rem;
  background: var(--bg-gray-100);
  font-weight: 600;
}
.tx-table td {
  border-bottom: 1px solid #e5e7eb;
  padding: 0.41rem 0.45rem;
}
.tx-table tr:last-child td {
  border-bottom: none;
}
@media (max-width: 600px) {
  .dashboard-container, .form-container {
    padding: 0.6rem 0.2rem;
    border-radius: var(--radius-lg);
    margin: 1rem 0.3rem;
    max-width: 98vw;
  }
  .account-card {
    min-width: 92vw;
    padding: 1rem;
    font-size: 1rem;
  }
  .tx-table td, .tx-table th {
    font-size: 0.96rem;
    padding: 0.27rem 0.32rem;
  }
}
